<script>
    import StudentReg from "./registration/StudentReg.svelte";
    import InstructorReg from "./registration/InstructorReg.svelte";
    import KamaraReg from "./registration/KamaraReg.svelte";
    import GuestReg from "./registration/GuestReg.svelte";
    import StudentSuccess from "./success/StudentSuccess.svelte";
    import InstructorSuccess from "./success/InstructorSuccess.svelte";
    import ChamberSuccess from "./success/ChamberSuccess.svelte";
    import GuestSuccess from "./success/GuestSuccess.svelte";
    import Header from "./presentational/Header.svelte";
    import Menu from "./presentational/Menu.svelte"
    import Notification from "./presentational/Notification.svelte";
    import Footer from "./presentational/Footer.svelte";
    import HandleFetch from "../services/handle-fetch.ts";
    import {writable} from "svelte/store";
    import {location} from 'svelte-spa-router';
    import "bulma-extensions/bulma-pageloader/dist/css/bulma-pageloader.min.css";

    export let pathApi = writable("");
    let done = writable(false);
    let data = writable({});
    let type = writable({});
    let responseData = writable({});

    let studentApi = "/post/studentPost";
    let instructorApi = "/post/instructorPost";
    let guestApi = "/post/guestPost";
    let chamberApi = "/post/chamberPost";
    let pageLoader = false;


    function handleMessage(event) {
        $type = event.detail.text;


        if ($type === 'student') {
            $pathApi = studentApi;
        } else if ($type === 'instructor') {
            $pathApi = instructorApi;
        } else if ($type === 'guest') {
            $pathApi = guestApi;
        } else if ($type === 'chamber') {
            $pathApi = chamberApi;
        }
        $data = event.detail.data;


        pageLoader = true
        fetch($pathApi, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify($data)

        }).then(HandleFetch)

            .then(res => {
                if (res.statusCode === 200) {
                    $responseData = res;
                    $done = true;

                }
            }).finally(()=> pageLoader=false)
    }
</script>

<div class="pageloader is-info is-bottom-to-top" class:is-active={pageLoader} class:is-hidden={!pageLoader}><span class="title">Kérlek várj!<br>Email küldése folyamatban!</span></div>

{#if $done}
    {#if $location === "/student"}
        <Menu/>
        <Notification text="Sikeres regisztráció" />
        <StudentSuccess formData={$data} responseData={$responseData}/>
        <Footer/>
    {/if}
    {#if $location === "/instructor"}
        <Menu/>
        <Notification text="Sikeres regisztráció" />
        <InstructorSuccess formData={$data} responseData={$responseData}/>
        <Footer/>
    {/if}
    {#if $location === "/guest"}
        <Menu/>
        <Notification text="Sikeres regisztráció" />
        <GuestSuccess formData={$data} responseData={$responseData}/>
        <Footer/>
    {/if}
    {#if $location === "/chamber"}
        <Menu/>
        <Notification text="Sikeres regisztráció" />
        <ChamberSuccess formData={$data} responseData={$responseData}/>
        <Footer/>
    {/if}

{/if}
{#if $done === false}
    {#if $location === "/student"}
        <Header title="Hallgatói Regisztráció" subtitle="Regisztrálj, hogy le ne maradj!"/>
        <StudentReg on:message={handleMessage}/>
        <Footer/>
    {/if}
    {#if $location === "/instructor"}
        <Header title="Oktatói regisztráció" subtitle="Regisztrálj, hogy le ne maradj!"/>
        <InstructorReg on:message={handleMessage}/>
        <Footer/>
    {/if}
    {#if $location === "/guest"}
        <Header title="Vendég regisztráció" subtitle="Regisztrálj, hogy le ne maradj!"/>
        <GuestReg on:message={handleMessage}/>
        <Footer/>
    {/if}
    {#if $location === "/chamber"}
        <Header title="Kamarai regisztráció" subtitle="Regisztrálj, hogy le ne maradj!"/>
        <KamaraReg on:message={handleMessage}/>
        <Footer/>
    {/if}
{/if}
