<script>
    import Instructor from '../form/InstructorForm.svelte';
    import QRCodeStyling from "../../script/qr-code-styling";
    import {onMount} from "svelte";

    window.scrollTo(0,0);
    export let formData;
    export let responseData;

    export let qrCode = new QRCodeStyling({
        width: 300,
        height: 300,
        type: "svg",
        data: responseData.qrCode,
        // image: "frontend/web/src/elements/assets/mik_favico.svg",
        image: "data: image/png;base64,",
        dotsOptions: {
            color: "#6a1a4c",
            type: "dots",
        },
        backgroundOptions: {
            color: "#ffffff",
            gradient: null
        },
        imageOptions: {
            crossOrigin: "anonymous",
            margin: 2,
            hideBackgroundDots: true,
            imageSize: 0.5
        },
        dotsOptionsHelper: {
            colorType: {
                single: true,
                gradient: false
            }
        },
        cornersSquareOptions: {
            type: "extra-rounded",
            color: "#000000",
        },
        cornersDotOptions: {
            "type": "square",
            "color": "#000000",
        }
    });

    onMount(async () => {
        qrCode.append(document.getElementById("canvas"));
    });
</script>



<div class='columns is-mobile is-centered'>
    <div class='column is-7-desktop column is-10-mobile'>
        <div class="card mb-6">
            <div class="is-divider is-info mt-6" data-content="QR KÓD"></div>
            <div class="card">
                <header class="card-header">
                    <p class="card-header-title">
                        Az alábbi QR kód használatával tud résztvenni az eseményen.
                    </p>
                </header>
                <div class="card-content">
                    <div class="content">
                        <div class="canvasHolder" id="canvas"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
<Instructor formData={formData}/>
<style>
    .canvasHolder {
        width: 300px;
        margin: 0 auto 0 auto;
    }
