<script>

    import {writable} from "svelte/store";

    let loading = false
    let done = writable(false);
    let responseData = writable({});
    let code = writable([]);
    let student = [];
    let instructor = [];
    let guest = [];
    let chamber = [];
    let majors = [];
    //let text = writable("");
    export let params = {};

    fetch('http://thanos.localhost:8080/info/' + params.code)
        .then(res => res.json())
        .then(data => {
            if (data !== false && data !== null) {
                $code = data;
                student = $code.student;
                instructor = $code.instructor;
                guest = $code.guest;
                chamber = $code.chamber;
                majors = $code.majors;

                $done = true;
            }
        });

</script>

<div class="container has-background-light">
    <div class="card-header">
        <p class="card-header-title">Statisztika</p>
        <div class="card-content">
            <p>Regisztrált hallgatók száma: <b>{student.length}</b></p>
        </div>
    </div>
</div>

<div class="container is-fullwidth">
    <div class="b-table">
        <div class="table-wrapper has-mobile-cards">
            <table class="table is-fullwidth is-striped is-hoverable is-fullwidth dataTable">
                <thead>
                <tr role="row">
                    <th class="sorting sorting-asc" aria-sort="ascending">Név</th>
                    <th class="sorting">Neptun kód</th>
                    <th class="sorting">Regisztráció ideje</th>
                    <th class="sorting">Szak</th>
                    <th class="sorting">Email</th>
                    <th class="sorting">Szemester</th>
                    <th class="sorting">Mobil</th>
                </tr>
                </thead>
                <tbody>

                {#each student as user}

                    <tr>
                        <td>{user.name}</td>
                        <td>{user.neptun}</td>
                        <td><small class="has-text-grey is-abbr-like">{user.created}</small></td>
                        <td>
                            {#each majors as major}
                                {#if user.major === major.id}
                                    {major.name}
                                {/if}
                            {/each}
                        </td>
                        <td>{user.email}</td>
                        <td>{user.semester}. szemeszter</td>
                        <td>{user.mobile}</td>
                        <td class="buttons is-right">
                            <button class="button is-small is-info" type="button" value="{user.id}">
                            <span class="icon">
                                <b class="mdi mdi-eye">
                                    +
                                </b>
                            </span>
                            </button>
                            <button class="button is-small is-danger" type="button" value="{user.id}">
                            <span class="icon">
                                <b class="mdi mdi-eye">
                                    -
                                </b>
                            </span>
                            </button>
                        </td>
                    </tr>
                {/each}


                </tbody>
                <tfoot>
                <tr>
                    <th class>Név</th>
                    <th>Neptun kód</th>
                    <th>Regisztráció ideje</th>
                    <th>Szak</th>
                    <th>Email</th>
                    <th>Szemester</th>
                    <th>Mobil</th>
                </tr>
                </tfoot>
            </table>
        </div>
    </div>
</div>


